document.addEventListener('DOMContentLoaded', function() {
    // Create the map
    var map = L.map('map').setView([41.42094, 21.93514], 6); // Centered around MKD

    // Set up the OSM layer
    L.tileLayer('https://{s}.basemaps.cartocdn.com/dark_all/{z}/{x}/{y}.png', {
        attribution: '© OpenStreetMap contributors, © CartoDB'
    }).addTo(map);


    // Define intensity color scale
    var intensityColor = d3.scaleLinear()
        .domain([300, 350])
        .range(['orange', 'red'])
        .clamp(true);

    // Fetch wildfire data from API and add points to map
    //    d3.csv("https://firms.modaps.eosdis.nasa.gov/api/country/csv/309b61cec3eaef6670156ea8ba0289dd/VIIRS_SNPP_NRT/MKD/4/2023-10-05").then(data => {

    d3.csv("https://firms.modaps.eosdis.nasa.gov/api/country/csv/309b61cec3eaef6670156ea8ba0289dd/VIIRS_SNPP_NRT/MKD/4/2023-10-05").then(data => {
        data.forEach(d => {
            // Create a circle marker for each data point
            var circle = L.circleMarker([+d.latitude, +d.longitude], {
                color: intensityColor(+d.bright_ti4), // Color based on intensity
                radius: 4 // Set radius of circle
            }).addTo(map);

            // Optional: Add tooltip to circle marker
            circle.bindTooltip(`Intensity: ${d.bright_ti4}`);
        });
    });
});
